import React, { Component } from "react";
import { Col, Jumbotron } from "reactstrap";
import { Link } from "react-router-dom";

export default class MainPageHeader extends Component {
  render() {
    return (
      <div>
        <Col md={12}>
          <Jumbotron>
            <h1 className="display-3">Welcome!</h1>
            <br />
            <p className="lead">Use Scheduler to get everything done.</p>
            <p className="lead">
              Easily switch between personal tasks and group tasks.
            </p>
            <p className="lead">Track the progress of your tasks everyday.</p>
            <hr className="my-2" />

            <br />
          </Jumbotron>
        </Col>
      </div>
    );
  }
}
